@-webkit-keyframes column-up {
  from {
    -webkit-transform: rotate(1deg) translateY(0px);
            transform: rotate(1deg) translateY(0px); }
  to {
    -webkit-transform: rotate(1deg) translateY(-2000px);
            transform: rotate(1deg) translateY(-2000px); } }

@keyframes column-up {
  from {
    -webkit-transform: rotate(1deg) translateY(0px);
            transform: rotate(1deg) translateY(0px); }
  to {
    -webkit-transform: rotate(1deg) translateY(-2000px);
            transform: rotate(1deg) translateY(-2000px); } }
@-webkit-keyframes column-down {
  from {
    -webkit-transform: rotate(1deg) translateY(-2000px);
            transform: rotate(1deg) translateY(-2000px); }
  to {
    -webkit-transform: rotate(1deg) translateY(0px);
            transform: rotate(1deg) translateY(0px); } }
@keyframes column-down {
  from {
    -webkit-transform: rotate(1deg) translateY(-2000px);
            transform: rotate(1deg) translateY(-2000px); }
  to {
    -webkit-transform: rotate(1deg) translateY(0px);
            transform: rotate(1deg) translateY(0px); } }
